@charset "utf-8";
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'HakgyoansimDunggeunmisoTTF-B';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2408-5@1.0/HakgyoansimDunggeunmisoTTF-B.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

body{
  margin: 0;
  padding: 0;
  font-family: 'Pretendard-Regular';
  font-weight: 400;
  font-style: normal;
}

.App {
  max-width: 1280px;
  text-align: center;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}
.App>div{
  margin: 60px auto;
}
button{
  border:none;
  border-radius: 20px;
  background: #000;
  color:aliceblue;
  padding: 8px 20px;
  cursor: pointer;
  margin-bottom: 20px;
}
h2{
  font-family: 'HakgyoansimDunggeunmisoTTF-B';
  font-weight: 700;
  font-style: normal;
  font-size: 2.5rem;
  margin-bottom: 20px;
  padding-bottom: 0;
}

.random-result{
  border: 1px solid #000;
  margin: 0 auto;

}
.irr_box .random-result{
  text-align: left;
  padding: 20px;
}